body{
  margin: 0;
  padding: 0;
  font-family: cursive;
  background: #11121b;
}
.App{
  width: 50%;
  margin: auto;
  background: #071724;
  margin-top:3rem;
  padding: 1rem;
}
.add-task{
  display:'block';
  color: #06cbb8;
  width:'fit-content';

}
.add-task:hover{
